import React from 'react';

class Dropdown extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      isHidden: props.isHidden,
    }

    this.checkHidden = this.checkHidden.bind(this);
  }

  checkHidden () {
    console.log(this.state.isHidden);
    if (this.state.isHidden === true) {
      return "hidden";
    } else {
      return "";
    }
  }

  static getDerivedStateFromProps(props, state) {
    console.log(props);
    console.log(props.isHidden)
    if (props.isHidden) {
      return {
        isHidden: props.isHidden
      }
    }
  }

  componentDidUpdate (prevState) {
    console.log(this.state);
  }

  render () {

    return(
      <div className={`current-user-info ${this.checkHidden()}`}>
        <style jsx>{`
          .current-user-info {
            display: flex;
            overflow: hidden;
            width: 200px;
            height: 200px;
            background: white;
          }
          .hidden {
            display: none;
          }

        `}</style>
          <div className="current-user-name">Current User</div>
          <div className="current-user-email">CurrentUser@gmail.com</div>
          <a className="current-user-reports" href="https://publist.ai/reports">
          Dashboard
        </a>
        <a className="current-user-settings" href="https://publist.ai/settings">
          Settings
        </a>
      </div>
    )
  }
}

export default Dropdown;



